import { makeStyles } from '@material-ui/core'
import React from 'react'


interface Props {
    stepCount: number;
    isActive: boolean;
    txt: string;
}

const styles = makeStyles(() => ({
    container: {
        display: 'flex',
        position: "relative",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
        maxWidth: 140,
        "&::after": {
            content: '""',
            position: "absolute",
            width: 50,
            height: 50,
            borderLeft: 0,
            borderBottom: 0,
            border: "2px solid #F1F4F8",
            transform: "rotate(45deg)",
            right: -6
        }
    },
    circle: {
        width: 25,
        height: 25,
        borderRadius: "100%",
        display: "flex",
        alignItems: 'center',
        justifyContent: 'center',
        border: "1px solid #BDCAD8"
    },
    txt: {
        textAlign: 'center',
        minHeight: 50,
        marginBottom: 0
    }
}))

export const Step: React.FC<Props> = ({ stepCount, isActive, txt }) => {
    const c = styles();
    return (
        <div className={c.container}>
            <div className={c.circle} style={{ backgroundColor: isActive ? "#35CF94" : "white", color: isActive ? "white" : "black" }}>
                {stepCount}
            </div>
            <p className={c.txt}>{txt}</p>
        </div>
    )
}