import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { ReactComponent as Filter } from "assets/icons/dashboard/campaigns/filter.svg";
import shirt from "assets/icons/shirt.png";
import { Sidebar } from 'pages/dashboard/components';
import React from 'react';
import { useStyles } from './campaigns.style';
// import noCampaign from "assets/icons/dashboard/noCampaign.png";
import { Step } from './components';


const data = [
    {
        id: 1,
        url: shirt,
        name: "turkan",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },
    {
        id: 2,
        url: shirt,
        name: "good",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },
    {
        id: 1,
        url: shirt,
        name: "turkan",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },
    {
        id: 2,
        url: shirt,
        name: "good",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },
    {
        id: 1,
        url: shirt,
        name: "turkan",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },
    {
        id: 2,
        url: shirt,
        name: "good",
        saleCount: 0,
        totalEarning: "-",
        status: 0   // 0 waiting, 1 done
    },

]

export const Campaigns = () => {
    const c = useStyles();
    return (
        <>
            <Sidebar header="Campaigns" notOverflowed>
                <div className={c.pageContainer}>
                    <div className={c.stepContainer}>
                        <Step step={1} txt="Create your account" />
                        <Step step={2} txt="Launch your first campaign" />
                        <Step step={3} txt="Get your first sale" />
                        <Step step={4} txt="Sell the minimum to print" />
                    </div>
                    <div className={c.filterCont}>
                        <Paper className={c.paper} elevation={3}>
                            <p>Filter campaigns </p>
                            <Filter style={{ width: 20, height: "20px !important", userSelect: 'none', msUserSelect: "none" }} />
                        </Paper>
                        <button className="btn-primary btn-sm" style={{ width: 150 }}
                            onClick={() => { }} >
                            start new
                        </button>
                    </div>
                    <TableContainer className={c.tableContainer} >
                        <Table className={c.table}>
                            <TableHead >
                                <TableRow className="">
                                    <TableCell align="left" className={c.tableHeader}>Name</TableCell>
                                    <TableCell align="center" className={c.tableHeader}>Sale</TableCell>
                                    <TableCell align="center" className={c.tableHeader}>Total earned</TableCell>
                                    <TableCell align="left" className={c.tableHeader}>status</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {
                                    data.map(({ id, url, name, saleCount, totalEarning, status }) => (
                                        <TableRow key={id}>
                                            <TableCell align="left" >
                                                <div className={c.tableImageCont}>

                                                    <img src={url} className={c.tableImg} />
                                                    <p>{name}</p>
                                                </div>
                                            </TableCell>
                                            <TableCell className={c.datum} align="left">{saleCount}</TableCell>
                                            <TableCell className={c.datum} align="left">{totalEarning}</TableCell>
                                            <TableCell align="left">
                                                {
                                                    status === 0 &&
                                                    <div className={c.status}>
                                                        <div className={c.greenCircleCont}>
                                                            <div className={c.greenCircle}></div>
                                                            <p className={c.onOf}>on</p>
                                                        </div>
                                                        <p className={c.waiting} >Waiting for first order</p>
                                                    </div>
                                                }
                                            </TableCell>
                                        </TableRow>
                                    ))
                                }
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
            </Sidebar >
        </>
    )
}