import { Grid, Paper, Typography } from '@material-ui/core';
import { Sidebar } from 'pages/dashboard/components';
import React from 'react';
import { useStyles } from './help.style';
import backLeft from "assets/icons/dashboard/helpLeftBack.png"
import backRight from "assets/icons/dashboard/helpRightBack.png"
import { BonfireAccordion } from './component';

export const Help = () => {
    const c = useStyles()
    return (
        <>
            <Sidebar header="Help" bgColor="#F2F4F8">
                <div className={c.pageContainer}>
                    <Grid container>
                        <Grid item lg={6}>
                            <Paper className={c.boxes}>
                                <Typography variant="h4">For seller</Typography>
                                <div style={{ position: "relative", height: 120, margin: "30px 0px" }}>
                                    <img src={backLeft} />
                                    <Typography className={c.txt}>How T-Shirt Campaigns Work on Style Factory</Typography>
                                </div>
                                {
                                    [
                                        "How do Bonfire Campaigns work? ",
                                        "Where do you ship and what are the costs?",
                                        "When will items be delivered? ",
                                        "Is Bonfire for selling or fundraising? ",
                                        "Does Bonfire cost the seller? ",
                                        "How and when do I receive my profits? "
                                    ].map(
                                        txt =>
                                            <BonfireAccordion header={txt} text="" />
                                    )
                                }
                            </Paper>
                        </Grid>
                        <Grid item lg={6}>

                            <Paper className={c.boxes}>
                                <Typography variant="h4">For seller</Typography>
                                <div style={{ position: "relative", height: 120, margin: "30px 0px" }}>
                                    <img src={backRight} />
                                    <Typography className={c.txt}>How Style Factory Supply works </Typography>
                                </div>
                                {
                                    [
                                        " How does Supply work? ",
                                        " How does shipping work? ",
                                        " When will items be delivered? ",
                                        " Is there a minimum purchase quantity? ",
                                    ].map(
                                        txt =>
                                            <BonfireAccordion header={txt} text="You create or upload a custom design, choose sizes & quantities, and we ship everything in one package. From there, you have full control of the inventory: you can promote the apparel online, sell at an event, distribute to friends and familyits totally up to you." />
                                    )
                                }
                            </Paper>
                        </Grid>
                        <Grid item lg={6}>
                            <Paper className={c.boxes} style={{ margin: "40px 0px", width: "100%" }}>
                                <Typography variant="h4">Help Center</Typography>
                                <Typography style={{ fontSize: 15, maxWidth: 500, margin: "20px 0px" }}>
                                    Don't see what you're looking for? Check out our extensive help center with helpful guides on various topics.
                                </Typography>
                                <button className="btn-primary btn-sm"
                                    onClick={() => { }} >
                                    visit help center
                                </button>
                            </Paper>
                        </Grid>

                    </Grid>
                </div>
            </Sidebar >
        </>
    )
}