import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

interface Props {
    header: string;
    text: string;
}
const useStyles = makeStyles(() => ({
    root: {},
    heading: {
        color: "#5E82A6",
        fontSize: 18
    },
    accordion: {
        border: 0,
        boxShadow: "unset"
    },
    txt: {
        fontSize: 15,
        "&::first-letter": {
            marginLeft: "1em"
        }
    }
}));

export const BonfireAccordion: React.FC<Props> = ({ header, text }) => {
    const c = useStyles();

    return (
        <div className={c.root}>
            <Accordion className={c.accordion}>
                <AccordionSummary expandIcon={<ExpandMoreIcon color="secondary" fontSize="large" />}>
                    <Typography className={c.heading}>{header}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography className={c.txt}>
                        {text}
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
