import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
    pageContainer: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
    },
    header: {
        fontSize: 20,
        fontWeight: 600,
        marginBottom: 20,
        marginTop: 20,
    },
    stepContainer: {
        display: "flex",
        justifyContent: 'space-between',
        width: "100%",
        padding: `${theme.spacing(0)}px ${theme.spacing(20)}px`,
        marginBottom: "30px"
    },
    filterCont: {
        display: "flex",
        justifyContent: "space-between",
        height: 50,
        width: "100%",
        marginTop: 30,
        marginBottom: 50,
    },
    paper: {
        display: "flex",
        border: "0px",
        alignItems: "center",
        width: 180,
        padding: "0px 15px",
        justifyContent: "space-between",
        "& p": {
            lineHeight: 0,
            marginBottom: 0
        }
    },
    // table
    tableContainer: {
        width: "100%",
        overflowY: "scroll",
        height: 332,
    },
    table: {
    },
    tableHeader: {
        fontSize: 20,
        fontWeight: 600,
        textTransform: "uppercase"
    },
    tableImg: {
        width: 70,
    },
    tableImageCont: {
        display: 'flex',
        alignItems: "center",
        "& p": {
            marginBottom: 0,
            lineHeight: 0,
            fontSize: 15,
            paddingLeft: 30
        }
    },
    datum: {
        fontSize: 15,
        textAlign: "center"
    },
    status: {

    },
    greenCircle: {
        width: 18,
        height: 18,
        backgroundColor: theme.palette.primary.main,
        borderRadius: "100%"
    },
    onOf: {
        marginBottom: 0,
        lineHeight: 0,
        marginLeft: 10,
        fontSize: 15,
        fontWeight: 500
    },
    greenCircleCont: {
        display: "flex",
        alignItems: "center",
        marginBottom: 10
    },
    waiting: {
        fontSize: 13,
        width: 100
    }
}))