import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    sidebar_navbar_container: {
        display: "flex",
        height: "100vh",
        overflowY: "hidden"
    },
    // sidebar
    sidebar: {
        width: "200px",
        backgroundColor: "#222134",
        height: '100%',
    },
    logoContainer: {
        display: 'flex',
        padding: "40px 30px",
        alignItems: 'center',
        "& p": {
            lineHeight: "unset",
            marginBottom: "unset",
            letterSpacing: 1,
            fontWeight: 200,
            marginTop: 7
        }
    },
    logoText: {
        fontSize: "19px",
        fontWeight: 600,
        opacity: '.5',
        color: "white",
        marginLeft: 10
    },
    liMenu: {
        height: 370,
        display: "flex",
        flexDirection: 'column',
        justifyContent: 'space-between',
    },
    li: {
        display: "flex",
        justifyContent: 'flex-start',
        alignItems: 'center',
        letterSpacing: 1,
        borderLeft: "2px solid transparent",
        padding: "17px 30px",
        "& p": {
            fontSize: '16px',
            color: "white",
            fontWeight: 500,
            lineHeight: "unset",
            marginBottom: "unset"
        },
        "&:hover": {
            backgroundColor: "#3a3f4f",
            cursor: "pointer",
            borderLeft: "2px solid white",
            fontWeight: 600
        },
    },
    selectedLi: {
        backgroundColor: "#3a3f4f",
        borderLeft: "2px solid white",
        fontWeight: 600
    },
    goHome: {
        display: "flex", padding: "0px 12px", justifyContent: 'space-between',
        "&:hover": {
            cursor: "pointer",
            marginTop: "-1px"
        }
    },
    liLogo: {
        marginRight: "20px"
    },
    divider: {
        margin: "20px 0px"
    },
    // navbar
    navbar: {
        height: "100px",
        width: "100%",
        display: 'flex',
        padding: "40px",
        justifyContent: "space-between",
        alignItems: 'center',
        "& p": {
            lineHeight: "unset",
            marginBottom: "unset"
        }
    },
    profileContCont: {
        display: "flex",
        alignItems: "center",
        justifyContent: 'space-between',
        width: "200px"
    },
    profileCont: {
        display: "flex",
        alignItems: 'center'
    },
    pageName: {
        fontSize: "19px",
        fontWeight: 600,
        color: "black",
        marginLeft: 10,
        marginTop: 10
    },
    username: {

    },
    profile: {
        width: "45px",
        height: "45px",
        marginLeft: 10,
        borderRadius: "50%",
        padding: 2,
        border: "2px solid #DFE0EB"
    },
    // children 
    childrenContainer: {
        padding: "20px 50px",
        marginBottom: "10A0px",
        height: "100%"
    }
}))