import { Divider, Grid, InputBase, Tab, Tabs, Typography } from '@material-ui/core'
import { BonfireField } from 'components/BonfireField/BonfireField'
import { BonfireTabPanel } from 'components/BonfireTabPanel'
import { Form, Formik } from 'formik'
import { Sidebar } from 'pages/dashboard/components'
import React, { useState } from 'react'
import { useStyles } from './account.style'
import { AccountValidate } from './validation'


export const Account = () => {
    const [tabValue, setTabValue] = useState(0)

    const initialValues = {
        name: "",
        email: "",
        password: ""
    }
    const onSubmit = () => {

    }
    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }
    const c = useStyles()
    return (
        <>
            <Sidebar header="Account" notOverflowed>
                <Grid container style={{ height: "500px" }}>
                    <Grid container xs={12} >
                        <div className={c.circle}></div>
                        <Grid item className={c.firstTypo}>
                            <Typography variant="h2" style={{ fontWeight: 700, marginBottom: "20px" }}>Lorem Ipsum</Typography>
                            <Typography variant="h5" style={{ fontSize: "1.9rem" }}>Member since January,2021</Typography>
                        </Grid>
                    </Grid>
                    <Grid container className={c.inputContainer}>
                        <div>
                            <Tabs className={c.tabs} value={tabValue} onChange={(_, value) => setTabValue(value)} aria-label="simple tabs example">
                                <Tab label={<p className={c.tabName}>Profile Settings</p>} {...a11yProps(0)} />
                                <Tab label={<p className={c.tabName}>Password</p>} {...a11yProps(1)} />
                            </Tabs>
                        </div>
                        <Grid item xs={12}>
                            <Divider />
                        </Grid>
                        <Formik
                            initialValues={initialValues}
                            validationSchema={AccountValidate}
                            onSubmit={onSubmit}
                        >
                            {({ submitForm }): React.ReactNode => {
                                return (
                                    <Form>
                                        <BonfireTabPanel index={0} value={tabValue} className={c.tabPanel}>
                                            <Grid item xs={12}>
                                                <BonfireField
                                                    name="name"
                                                    placeholder="Name"
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <BonfireField
                                                    name="email"
                                                    placeholder="Email Address"
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <Typography variant="h4" style={{ fontWeight: 700, marginTop: "20px" }}>Image</Typography>
                                                <InputBase type="file" style={{ fontSize: "16px", marginTop: "10px" }} />
                                            </Grid>
                                            <Grid item xs={12} style={{ display: "flex", alignItems: "center", marginTop: 10 }}>
                                                <Typography style={{ fontSize: 15, marginRight: 10 }}>JPG or PNG. Maximum size of 5 MB.</Typography>
                                                <Typography style={{ fontSize: 13, color: "#79AAE7" }}>Delete photo</Typography>
                                            </Grid>
                                            <button type="submit" style={{ marginTop: '20px' }} className={`btn-primary btn-sm`} onClick={submitForm}>Save Changes</button>
                                        </BonfireTabPanel>
                                        <BonfireTabPanel index={1} value={tabValue} className={c.tabPanel}>
                                            <Grid item xs={12}>
                                                <BonfireField
                                                    name="password"
                                                    placeholder="Password"
                                                    type="password"
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <BonfireField
                                                    name="confirmPassword"
                                                    placeholder="Confirm Password"
                                                    type="password"
                                                />
                                            </Grid>
                                            <button type="submit" style={{ marginTop: '20px' }} className={`btn-primary btn-sm`} onClick={submitForm}>Change password</button>
                                        </BonfireTabPanel>
                                    </Form>
                                )
                            }}
                        </Formik>
                    </Grid>
                </Grid>
            </Sidebar >
        </>
    )
}
