import { Divider, Grid, MenuItem, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Tooltip, Zoom } from '@material-ui/core'
import { BonfireField } from 'components/BonfireField/BonfireField'
import { Form, Formik } from 'formik'
import { Sidebar } from 'pages/dashboard/components'
import React from 'react'
import { useStyles } from './myOrders.style'
import gear from 'assets/icons/gear.png'
import shirt from 'assets/icons/shirt.png'


const data = [
    {
        id: 1,
        date: "Feb 7, 2021",
        order: "#D - 21 - 5504 - 822",
        details: "Sweets Order",
        total: "---"
    },
    {
        id: 2,
        date: "Feb 7, 2021",
        order: "#D - 21 - 5504 - 822",
        details: "Sweets Order",
        total: "---"
    },
    {
        id: 1,
        date: "Feb 7, 2021",
        order: "#D - 21 - 5504 - 822",
        details: "Sweets Order",
        total: "---"
    },
    {
        id: 2,
        date: "Feb 7, 2021",
        order: "#D - 21 - 5504 - 822",
        details: "Sweets Order",
        total: "---"
    },
]

export const MyOrders = () => {
    const c = useStyles()

    return (
        <>
            <Sidebar header="My Orders" notOverflowed>
                <Grid className={c.container}>
                    <div className={c.dropDownContainer}>
                        <Formik
                            initialValues={{
                                dropDown: 0
                            }}
                            onSubmit={() => { }}
                        >
                            {({ values }): React.ReactNode => {
                                return (
                                    <Form>
                                        <BonfireField
                                            name="dropDown"
                                            placeholder=""
                                            value={values.dropDown}
                                            select={true}
                                            className={c.dropDown}
                                        >
                                            <MenuItem style={{ fontSize: "17px" }} value={0}>ALL ORDERS (1)</MenuItem>
                                            <MenuItem style={{ fontSize: "17px" }} value={1}>All Orders</MenuItem>
                                        </BonfireField>
                                    </Form>
                                )
                            }}
                        </Formik>
                    </div>
                    <Divider style={{ margin: "20px 0px" }} />

                    <div>
                        <TableContainer className={c.table}>
                            <Table>
                                <TableHead>
                                    <TableRow className={c.headerCont}>
                                        <TableCell align="left">DATE</TableCell>
                                        <TableCell align="left">ORDERS#</TableCell>
                                        <TableCell align="left">DETAILS</TableCell>
                                        <TableCell align="left">TOTALS</TableCell>
                                        <div className={c.draftContainer}>
                                            <p>Draft</p>
                                            <Tooltip TransitionComponent={Zoom} placement="top"
                                                title={
                                                    <div className={c.help}>
                                                        <p>Click on this gear to reorder, edit drafts or start a campaign with an existing design.</p>
                                                    </div>
                                                }
                                            >
                                                <img src={gear} className={c.img} />
                                            </Tooltip>
                                        </div>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {
                                        data.map(({ date, id, details, order, total }) => (
                                            < TableRow key={id} >
                                                <TableCell className={c.simple} style={{ fontWeight: 600 }} align="left">{date}</TableCell>
                                                <TableCell className={c.productShirt} align="left" component="th" scope="row">
                                                    <p>{order}</p>
                                                    <img src={shirt} className={c.shirtImg} />
                                                </TableCell>
                                                <TableCell align="left" className={c.simple}>{details}</TableCell>
                                                <TableCell align="left" className={c.simple}>{total}</TableCell>
                                            </TableRow>
                                        ))
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </div>
                </Grid>
            </Sidebar >
        </>
    )
}
