import { Divider, Typography } from '@material-ui/core'
import browserHistory from 'browser-history/browser-history'
import clsx from 'clsx'
import { ReactComponent as ArrowDown } from 'img/sidebar/arrow_down.svg'
import { ReactComponent as Order } from 'img/sidebar/order.svg'
import { ReactComponent as Campaign } from 'img/sidebar/campaign.svg'
import { ReactComponent as Help } from 'img/sidebar/help_outline.svg'
import { ReactComponent as Logo } from 'img/sidebar/logo.svg'
import { ReactComponent as Money } from 'img/sidebar/money.svg'
import { ReactComponent as Overview } from 'img/sidebar/overview.svg'
import { ReactComponent as Profile } from 'img/sidebar/profile.svg'
import { ReactComponent as Store } from 'img/sidebar/store.svg'
import React from 'react'
import { useStyles } from './sidebar.style'

interface SideBarProps {
    header: string;
    children: any;
    notOverflowed?: boolean;
    bgColor?: string;
}

export const Sidebar: React.FC<SideBarProps> = ({ header, children, notOverflowed, bgColor }) => {
    const classes = useStyles()
    return (
        <div className={classes.sidebar_navbar_container} style={{ backgroundColor: bgColor }}>
            <div className={classes.sidebar}>
                <div className={classes.logoContainer}>
                    <Logo />
                    <Typography className={classes.logoText}>Dashboard</Typography>
                </div>
                <ul className={classes.liMenu}>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard")}>
                        <Overview className={classes.liLogo} />
                        <Typography>Overview </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/campaigns" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/campaigns")}>
                        <Campaign className={classes.liLogo} />
                        <Typography>Campaigns </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/store" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/store")}>
                        <Store className={classes.liLogo} />
                        <Typography>Store </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/payouts" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/payouts")}>
                        < Money className={classes.liLogo} />
                        <Typography>Payouts </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/my-orders" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/my-orders")}>
                        < Order className={classes.liLogo} />
                        <Typography>My orders </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/account" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/account")}>
                        < Profile className={classes.liLogo} />
                        <Typography>Account </Typography>
                    </li>
                    <li className={clsx(classes.li, window.location.pathname === "/dashboard/help" && classes.selectedLi)} onClick={() => browserHistory.push("/dashboard/help")}>
                        <Help className={classes.liLogo} />
                        <Typography>Help </Typography>
                    </li>
                    <Divider className={classes.divider} style={{ backgroundColor: "#BBBAC7", marginTop: 80 }} />
                    <div className={classes.goHome} onClick={() => browserHistory.push("/")}>
                        <Typography style={{ color: "white", margin: 0, fontSize: 15, marginTop: "-2px" }}>{"<"}</Typography>
                        <Typography style={{ color: "white", margin: 0, fontSize: 13 }}>Back to Style Factory</Typography>
                    </div>
                </ul>
            </div>
            <div style={{ width: "100%", height: "100%", overflowY: notOverflowed ? "hidden" : "scroll", marginBottom: "100px !important" }}>
                {/* navbar */}
                <div className={classes.navbar}>
                    <Typography className={classes.pageName}>{header}</Typography>
                    <div className={classes.profileContCont}>
                        <ArrowDown />
                        <div className={classes.profileCont}>
                            <Typography className={classes.username}>Lorem Ipsum</Typography>
                            <img src="https://via.placeholder.com/50" className={classes.profile} />
                        </div>
                    </div>
                </div>

                <div className={classes.childrenContainer} >
                    {children}
                </div>
            </div>
        </div >
    )
}