import { Grid, Tab, Tabs } from '@material-ui/core'
import payout from 'assets/icons/payout.png'
import payoutsOrganization from 'assets/icons/dashboard/payoutsOrganization.png'
import { BonfireTabPanel } from 'components/BonfireTabPanel'
import { Sidebar } from 'pages/dashboard/components'
import React, { useState } from 'react'
import { useStyles } from './payouts.style'


export const Payouts = () => {
    const [tabValue, setTabValue] = useState(0)
    const c = useStyles()
    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }



    return (
        <>
            <Sidebar header="Payouts" notOverflowed>
                <Grid container className={c.container}>
                    <div>
                        <Tabs className={c.tabs} value={tabValue} onChange={(_, value) => setTabValue(value)} aria-label="simple tabs example">
                            <Tab label={<p className={c.tabName}>My Payouts</p>} {...a11yProps(0)} />
                            <Tab label={<p className={c.tabName}>For an Organization</p>} {...a11yProps(1)} />
                        </Tabs>
                    </div>
                    <BonfireTabPanel className={c.firstTab} value={tabValue} index={0}>
                        <div className={c.imgContainer}>
                            <img draggable="false" src={payout} style={{ minHeight: '410px' }} />
                        </div>
                        <div style={{ marginTop: "10px", textAlign: "center", fontSize: 18 }}>
                            <h1>No available payouts yet</h1>
                        </div>
                        <div style={{ marginTop: "20px", display: "flex", justifyContent: 'center', fontSize: 18 }}>
                            <p style={{ fontWeight: 500, textAlign: "center", width: 300 }}>Dont worry, we will email you when the first one is ready.</p>
                        </div>
                    </BonfireTabPanel>
                    <BonfireTabPanel className={c.firstTab} value={tabValue} index={1}>
                        <div className={c.imgContainer}>
                            <img draggable="false" src={payoutsOrganization} style={{ minHeight: '410px' }} />
                        </div>
                        <div style={{ marginTop: "20px", display: "flex", justifyContent: 'center', fontSize: 18 }}>
                            <p style={{ fontWeight: 500, textAlign: "center", width: 400 }}>Raise money for a nonprofit organization of your choice. When the payout is ready, it will appear here.</p>
                        </div>
                    </BonfireTabPanel>
                </Grid>
            </Sidebar >
        </>
    )
}
