import * as Yup from "yup";

function equalTo(ref, msg) {
  return Yup.mixed().test({
    name: "equalTo",
    exclusive: false,
    message: msg || "${path} must be the same as ${reference}",
    params: {
      reference: ref.path,
    },
    test: function (value) {
      return value === this.resolve(ref);
    },
  });
}

Yup.addMethod(Yup.string, "equalTo", equalTo);

export const AccountValidate = Yup.object().shape({
  name: Yup.string().required("Mlumat zruridir"),
  email: Yup.string()
    .email("Format dzgn deyil")
    .required("Mlumat zruridir"),
  password: Yup.string().required("Mlumat zruridir"),
  // .matches(
  //     regexPassword,
  //     'n az 6 simvoldan ibart olmaldr',
  // ),
  confirmPassword: Yup.string()
    .equalTo(Yup.ref("password"), "ifrlr eyni deyil!")
    .required("Mlumat zruridir"),
});
