import { makeStyles } from '@material-ui/core'
import React from 'react'
import { ReactComponent as Account } from "assets/icons/dashboard/campaigns/account.svg"
import { ReactComponent as Campaign } from "assets/icons/dashboard/campaigns/campaign.svg"
import { ReactComponent as Cash } from "assets/icons/dashboard/campaigns/cash.svg"
import { ReactComponent as Favorite } from "assets/icons/dashboard/campaigns/favorite.svg"

interface Props {
    step: number;
    txt: string;
}

const useStyles = makeStyles(() => ({
    container: {
        minHeight: 70,
        position: "relative",
        display: 'flex',
        flexDirection: 'column',
        alignItems: "center",
        "&::after": {
            content: '""',
            position: "absolute",
            right: -40,
            top: "50%",
            fontSize: 40,
            width: 18,
            height: 18,
            border: "1.3px solid #BDCAD8",
            borderLeft: 0,
            borderBottom: 0,
            transform: "rotate(45deg)"
        }
    },
    img: {
        width: 60,
        height: 70
    },
    txt: {
        marginBottom: 0,
        fontSize: 12,
        width: 100,
        textAlign: "center"
    }
}))


export const Step: React.FC<Props> = ({ step, txt }) => {
    const c = useStyles();

    return (
        <>
            <div className={c.container}>
                {step === 1 && <Account className={c.img} />}
                {step === 2 && <Campaign className={c.img} />}
                {step === 3 && <Cash className={c.img} />}
                {step === 4 && <Favorite className={c.img} />}
                <p className={c.txt}>{txt}</p>
            </div>
        </>
    )
}