import { Sidebar } from 'pages/dashboard/components';
import React from 'react';
import { useStyles } from './store.style';
import box from '../../../../../src/img/box.png'

export const Store = () => {
    const c = useStyles()
    return (
        <>
            <Sidebar header="Store" notOverflowed>
                <div className={c.pageContainer}>
                    <div className={c.noStore}>
                        <div className={c.imgContainer}>
                            <img draggable="false" src={box} style={{ minHeight: '410px' }} />
                        </div>
                        <div style={{ marginTop: "10px", textAlign: "center", fontSize: 18 }}>
                            <h1>This user has no store</h1>
                        </div>
                        <div style={{ marginTop: "20px", display: "flex", justifyContent: 'center', fontSize: 18 }}>
                            <p style={{ fontWeight: 500, textAlign: "center", width: 300 }}>You can create a store</p>
                        </div>
                    </div>
                </div>
            </Sidebar >
        </>
    )
}