import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    pageContainer: {
    },
    boxes: {
        boxShadow: "none",
        padding: "20px",
        width: "95%",
        "& h4": {
            fontWeight: 600
        }
    },
    txt: {
        fontSize: 15,
        position: "absolute",
        top: 50,
        left: 20
    },
    blueLink: {
        position: "relative",
        color: "#5E82A6",
        fontSize: 16,
        "&:hover": {
            cursor: "pointer",
            color: "#7f9cb8",
        },
        "&::after": {
            content: '""',
            position: "absolute",
            width: 10,
            height: 10,
            border: "1px solid #5E82A6",
            borderLeft: 0,
            borderTop: 0,
            transform: "rotate(45deg)",
            top: 4,
            left: "60%"
        }
    }
}))