import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
    pageContainer: {
        display: "flex",
    },
    leftDiv: {
        backgroundColor: "#E6ECF3",
        padding: "20px",
        border: "2px solid #DCD9D5",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
        marginRight: 20
    },
    title: {
        fontSize: 18,
        fontWeight: 600,
        alignSelf: "flex-start"
    },
    subtitle: {
        fontSize: 14,
        textAlign: "center"
    },
    rightDiv: {
        marginLeft: 20,
        padding: "20px",
        border: "2px solid #DCD9D5",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
        backgroundColor: "#F7F6F4",
        height: "fit-content"
    },
    rightFirstLine: {
        display: "flex",
        justifyContent: "space-between",
        width: "100%",

    },
    firstCont: {
        height: "fit-content",
        minHeight: 600,
        display: "flex",
        justifyContent: "space-between",
        width: "100%"
    },
    boxHeader: {
        fontWeight: 600,
        marginBottom: 20
    },
    blueLink: {
        color: "#5E82A6",
        fontSize: 16
    },
    stepCont: {
        display: "flex",
        justifyContent: "space-between",
        padding: "0px 10px"
    },
    boxes: {
        borderRadius: 0,
        boxShadow: "none",
        padding: "20px",
        width: "100%"
    },
    jf_sb: {
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center"
    },
    recentSales: {
        height: "45%"
    },
    noSales: {
        textAlign: "center",
        fontSize: 17,
        maxWidth: 370,
        marginTop: 50
    },
    cntrd: {
        display: "flex",
        justifyContent: "center",
    },
    recentTitle: {
        letterSpacing: 2,
        fontSize: 16,
        fontWeight: 100,
        marginTop: 30
    },
    // table part
    tableContainer: {
        width: "100%",
        overflowY: "scroll",
        height: 332,
    },
    table: {
    },
    tableImg: {
        width: 70,
    },
    tableImageCont: {
        display: 'flex',
        alignItems: "center",
        "& p": {
            marginBottom: 0,
            lineHeight: 0,
            fontSize: 15,
            paddingLeft: 30,
            color: "#5E82A6",
        }
    },
    greenCircle: {
        width: 18,
        height: 18,
        backgroundColor: theme.palette.primary.main,
        borderRadius: "100%"
    },
    onOf: {
        marginBottom: 0,
        lineHeight: 0,
        marginLeft: 10,
        fontSize: 15,
        fontWeight: 500
    },
    greenCircleCont: {
        display: "flex",
        alignItems: "center",
        marginBottom: 10
    },
    waiting: {
        fontSize: 13,
        width: 100
    },
    guide: {
        width: "50%",
        minWidth: 300,
        position: "relative",
        height: 80,
        borderRadius: 15,
        backgroundColor: "#F1F4F8",
        padding: 20,
        "& h5": {
            fontWeight: 600
        },
        "&::after": {
            content: '""',
            width: 10,
            height: 10,
            borderLeft: 0,
            borderBottom: 0,
            border: '1px solid #BDCAD8',
            position: "absolute",
            right: 20,
            top: "50%",
            transform: "rotate(45deg)",
        }
    },
    txt: {
        minHeight: 50,
        marginBottom: 0,
        fontSize: 14
    },
    // payouts
    tabName: {
        marginBottom: 0,
        fontWeight: 600,
        fontSize: 16,
        width: "fit-content",
        textTransform: "none",
    },
    copyCont: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginLeft: 30,
        "&:hover": {
            cursor: "pointer"
        }
    },
    copyUrl: {
        color: "#BDCAD8",
        fontSize: 15
    },
    iconCont: {
        display: "flex"
    },
    social: {
        "&:hover": {
            cursor: "pointer"
        }
    },
    learnMore: {
        width: 200,
        fontSize: 16,
        textTransform: "uppercase",
        backgroundColor: "transparent",
        border: "1px solid #BDCAD8",
        borderRadius: 5,
        padding: "10px 20px"
    }
}))